<script setup>
import { onMounted, ref } from "vue";


const typeValue = ref('');
var typeStatus = false
const typeArray = ['Web Developer', 'Security Analyst']
const typingSpeed = 200
const erasingSpeed = 100
const newTextDelay = 2000
var typeArrayIndex = 0
var charIndex = 0
const typeText =()=>{
        if(charIndex < typeArray[typeArrayIndex].length){
            if(!typeStatus)
                typeStatus = true;

            typeValue.value += typeArray[typeArrayIndex].charAt(charIndex)
            charIndex += 1;
            setTimeout(typeText, typingSpeed)
        }else{
            typeStatus = false
            setTimeout(eraseText, newTextDelay)
        }
    }
const eraseText =()=>{
        if(charIndex > 0){
            if(!typeStatus)
                typeStatus = true;

                typeValue.value = typeArray[typeArrayIndex].substring(0, charIndex-1)
                charIndex -= 1
                setTimeout(eraseText, erasingSpeed)
        }else{
            typeStatus = false
            typeArrayIndex += 1;
            if(typeArrayIndex >= typeArray.length)
                typeArrayIndex = 0
            setTimeout(typeText, typingSpeed + 1000)
        }
}
onMounted(()=>{
    setTimeout(typeText(), newTextDelay + 200)
})
</script>
<template >
    <section>
        <div class="caption">
            <h1>Oke Oluwaseyi</h1>
            <h2 class="">I'm a 
                <span class="typed-text">{{ typeValue }}</span>
                <span class="cursor" :class="{'typing': typeStatus}">&nbsp;</span>
            </h2>
            <!-- <p class="tagline">Web Developer</p> -->
        </div>
    </section>
</template>

<style scoped>

