<script setup>
import { onMounted, ref } from 'vue';
// import Project from '@/MyComponents/ProjectComp.vue'
// import Wrapper from '@/MyComponents/FlexWrapper.vue'
import ProjectArticle from '@/MyComponents/ProjectArticle.vue'
import SectionLayout from '@/Layouts/SectionLayout.vue';
import projects from '@/assets/projects.json'
const data = projects.data
var currentIndex = 0
const currentImage = ref("")
const changeIndex =(index) => {
    if(index != currentIndex)
        currentIndex = index
        setImage()
}
const setImage =() =>{
    currentImage.value = data[currentIndex].img
}
onMounted(()=>{
    setImage()
})
</script>
<template id="projects">
    <SectionLayout >
        <template #header>
            Projects
        </template>
        <template #desc>
            Showcase of My Work
        </template>
        <div class="project-cont">
            <div class="figure-cont">
                <figure>
                    <div class="img-cont">
                        <img :src="currentImage" alt="">
                    </div>
                </figure>
            </div>
            <div class="projects-cont">
                <ul class="projects">
                    <li v-for="(item, index) in data" :key="item">
                        <ProjectArticle @click="changeIndex(index)" :class="{ 'active' : currentIndex == index }" :project="item" />
                        <hr>
                    </li>
                </ul>
            </div>
        </div>
    </SectionLayout>

</template>
<style scoped>
.figure-cont{
    display: flex;
    flex-direction: row;
    justify-content: right;
}
figure{
    height: 300px;
    border: 5px solid var(--primary);
    position: relative;
}
figure, .projects{
    flex: 1 1 300px;
    max-width: 500px;
}
.img-cont{
    transition: all ease-in-out 0.5s;
    overflow: hidden;
    height: 100%;
    width: 100%;
    position: absolute;
    right: -15px;
    bottom: -15px
}
.project-cont{
    display: grid;
    grid-template-columns: 0.5fr 0.5fr;
    align-items: center;
    gap: 2rem;
    justify-content: center;
}
li{
    max-width: 500px;
}
ul{
    max-height: 400px;
    overflow-y: auto;
    scrollbar-width: none;
}
@media (max-width: 1023px) {
    .project-cont{
        grid-template-columns: 1fr;
        /* grid-template-rows: 1fr 1fr; */
        /* justify-items: center; */
    }
    .figure-cont{
    justify-content: center;
    }
    .projects-cont{
        display: flex;
        flex-direction: row;
        /* align-items: center; */
        justify-content: center;
    }
}
@media (max-width: 425px) {
    figure{
        max-height: 250px;
    }
}
</style>